import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import user from '../user.png'
import notelogin from '../notelogin.png'
import Spinner from './Spinner';
export default function Login() {
  const [loading, setloading] = useState(false);
  const [credential, setCredential] = useState({ email: "", password: "" });
  let navigate = useNavigate();
  const handelSubmit = async (e) => {
    e.preventDefault();
    setloading(true);
    const response = await fetch("https://difficult-gold-bison.cyclic.app/api/auth/login", {
      method: 'POST',
      headers: {
        'content-type': 'application/json'
      },
      body: JSON.stringify({ email: credential.email, password: credential.password })
    });
    const json = await response.json();
    if (json.success) {
      localStorage.setItem('token', json.authtoken);
      navigate('/');
    }
    else {
      setloading(false);
      alert('invalid');
    }
  }
  const onChange = (e) => {
    setCredential({ ...credential, [e.target.name]: e.target.value })
  }
  return (
    <div>
    <div className="d-flex justify-content-between" >
      <form onSubmit={handelSubmit}>
        <h2 className='container my-4'>Login <img src={user} alt="login" style={{ height: '1em', marginInlineStart: '.5em' }} /> </h2>
        <div className="form-outline mb-2">
          <input type="email" id="email" style={{ width: "8cm" }} value={credential.email} className="form-control" name="email" onChange={onChange} placeholder='Email address' />
          <label className="form-label" htmlFor="email">Email address</label>
        </div>
        <div className="form-outline mb-2">
          <input type="password" style={{ width: "8cm" }} id="password" value={credential.password} className="form-control" name="password" onChange={onChange} placeholder='********' />
          <label className="form-label" htmlFor="password">Password</label>
        </div>

        <button type="submit" className="btn btn-primary btn-block mb-4" style={{ width: "2.5cm" }} value="submit" name="submit">Sign in</button>
        {loading && <Spinner/>}
      </form>
      <div className="image">
        <img src={notelogin} alt="notelogin" style={{height:'12cm', marginBlock:'4em',}} />
      </div>
    </div>
    
      </div>
  )
}
