import React,{useState} from 'react'
import {useNavigate} from 'react-router-dom'
import signuplogo from '../sibngp.jpg'
import Spinner from './Spinner';

export default function Signup() {
  const [loading , setloading] = useState(true)
  const [credential,setCredential]=useState({name:"",email:"",password:"",cpassword:""});
  let navigate = useNavigate();
  const handelSubmit = async (e)=>{
    e.preventDefault();
    setloading(false);
    const  {name,email,password} = credential;  
    const response = await fetch("https://difficult-gold-bison.cyclic.app/api/auth/createuser",{
    method: 'POST',
        headers: {
          'content-type': 'application/json'
        },
        body: JSON.stringify({name,email,password})
      }); 
     const json =  await response.json();
      if(json.success){
        localStorage.setItem('token',json.authtoken);
        navigate('/');
          }
      else{
        setloading(true);
        alert('invalid')
      }
       }
       const onChange=(e)=>{
        setCredential({...credential,[e.target.name]:e.target.value})
       }
  return (
    <div>
    <div className="d-flex justify-content-between">
   <form onSubmit={handelSubmit} >
   <h3 style={{marginBlock:'.4em'}}>Sign up <img src={signuplogo} alt="signuplogo"  style={{height:'1.5em'}}/> </h3>
   <div className="formsignup" >
   <div className="form-group">
    <label for="name">Enter your Name</label>
    <input type="text" style={{width:"8cm"}} className="form-control" id="name" aria-describedby="emailHelp" placeholder="Name" name="name" onChange={onChange}/>
  </div>
  <div className="form-group">
    <label for="email">Email address</label>
    <input type="email" style={{width:"8cm"}} className="form-control" id="email" aria-describedby="emailHelp" placeholder="Enter email" name="email" onChange={onChange}/>
    <small id="emailHelp" className="form-text text-muted">We'll never share your email with anyone else.</small>
  </div>
  <div className="form-group">
    <label htmlFor="password">Password</label>
    <input type="password" style={{width:"8cm"}} className="form-control" id="password" placeholder="******" name="password" onChange={onChange} minLength={5}/>
  </div>
  <div className="form-group">
    <label htmlFor="cpassword">Confirm Password</label>
    <input type="cpassword" style={{width:"8cm"}} className="form-control" id="cpassword" placeholder="confirm Password" name="cpassword" onChange={onChange} minlength={5}/>
  </div>
  <button type="submit" className="btn btn-primary" value="submit">Submit</button>
   </div>
</form>
<img style={{display: 'block',cursor: 'zoom-in',height:'12cm',marginBlockStart:'3em' }} src="https://eduvanz.com/assets/website1.0/images/sign-up.png" alt='singup'></img>
    </div>
    {!loading && <Spinner/>}
    </div>
  )
}
